import React, { useState } from 'react'
import './navbar.css'
import Logo from '../Images/AS_logo.png'
import {WavyLink} from 'react-wavy-transitions'
import MenuIcon from '../Images/icons8-menu-26.png'
import { useMediaQuery } from 'react-responsive'
import homeIcon from '../Images/home.png'
import skillIcon from '../Images/skill.png'
import aboutIcon from '../Images/about.png'
import contactIcon from '../Images/contact.png'


export function Navbar(){

  const mobileScreen = useMediaQuery({query:'(max-width:600px)'})
  const laptopScreen = useMediaQuery({query:'(min-width:600px)'})
  const [display,setDisplay] = useState(false);
  const delay = ms => new Promise(
    resolve => setTimeout(resolve, ms)
  );
  const [color,setColor] = useState('#0311fca7');

  return (
      <div id='navbar' style={{backgroundColor:color ,transition:"all 0.7s ease"}}>
        <div id="logo">
            <img id='logo-img' src={Logo} alt="" />
        </div>

        {display && mobileScreen?<div id="disp-title" style={{transition:"all ease"}}>
            <WavyLink  color="#0311fc" id='disp-title-home anchor' href="#intro" onClick={()=>{setDisplay(!display)}}>Home</WavyLink>
            <hr/>
            <WavyLink color="#EB017F" id='disp-title-skills anchor' href="#skill" onClick={()=>setDisplay(!display)}>Skills</WavyLink>
            <hr/>
            <WavyLink color="#8803fc" id='disp-title-about anchor' href="#about" onClick={()=>setDisplay(!display)}>About</WavyLink>
            <hr/>
            <WavyLink color="#fc0341" id='disp-title-contact anchor' href="" onClick={()=>setDisplay(!display)}>Contact</WavyLink>
        </div>:null}
        
        {mobileScreen ? <div id='Menu-div'>
          <img src={MenuIcon} onClick={()=>setDisplay(!display)} alt=""/>
        </div>:null}

        {laptopScreen? <div id="disp-title">
            <WavyLink color="#0311fc" exact to='/portfolio/' id='disp-title-home anchor' href="#intro"  ><img onClick={async ()=>{await delay(1000); setColor("#0311fca7")}} style={{width:"30px",height:"30px"}} src={homeIcon} alt=""/></WavyLink>
            <WavyLink color="#EB017F" exact to='/skills' id='disp-title-skills anchor' href="#skill" ><img onClick={async ()=>{await delay(1000); setColor("#EB017Fa7");}} style={{width:"30px",height:"30px"}} src={skillIcon} alt=""/></WavyLink>
            <WavyLink color="#8803fc" exact to='/about' id='disp-title-about anchor' href="#about"><img onClick={async ()=>{await delay(1000); setColor("#8803fca7");}} style={{width:"30px",height:"30px"}} src={aboutIcon} alt=""/></WavyLink>
            <WavyLink color="#fc0341" exact to='/contact' id='disp-title-contact anchor' href=""><img onClick={async ()=>{await delay(1000); setColor("#fc0341a7");}} style={{width:"30px",height:"30px"}} src={contactIcon} alt=""/></WavyLink>
        </div>:null}
        </div>
  
  )

}

  

export default Navbar